<template>
  <div>
    <el-form labelWidth="100px">
      <el-form-item label="">
        <el-switch v-model="attribute.showIndex" active-text="" inactive-text="" size="small"/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="attribute.theadHeight" size="small" :min="1" :max="800"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.theadBg[0]" size="mini"/>
        <el-color-picker v-model="attribute.theadBg[1]" size="mini"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.theadColor" size="mini"/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="attribute.theadSize" size="small" :min="1" :max="200"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.tbodyColor" size="mini"/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="attribute.tbodySize" size="small" :min="1" :max="200"/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.oddRowBg" size="mini" show-alpha/>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="attribute.evenRowBg" size="mini" show-alpha/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number v-model="attribute.showLine" size="small" :min="1" :max="200"/>
      </el-form-item>

      <div style="text-indent: 1em">
        <el-button size="small" theme="primary" @click="addCol"></el-button>
      </div>
      <el-table style="font-size: 12px;margin-top: 10px;" size="mini" :data="attribute.columns" @row-click="editRow">
        <el-table-column label="" prop="colKey"/>
        <el-table-column label="" prop="title"/>
        <el-table-column label="" prop="width"/>
      </el-table>
    </el-form>
    <el-dialog :title="currentRow.id?'':''" :visible.sync="modelShow" :append-to-body="true" width="400px">
      <el-form labelWidth="100px" size="mini">
        <el-form-item label="">
          <el-input v-model="currentRow.colKey"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currentRow.title"/>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="currentRow.type">
            <el-option label="" value="text"/>
            <el-option label="" value="img"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="currentRow.width" :min="0" :max="1000"/>
        </el-form-item>
        <el-form-item v-show="currentRow.id">
          <el-button theme="danger" @click="delCol"></el-button>
        </el-form-item>
        <el-button v-show="!currentRow.id" @click="confirmRow"></el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "cpt-scroll-table-option",
  props: { attribute: Object },
  data(){
    return {
      columns:[
        {colKey: 'colKey', title: ''},
        {colKey: 'title', title: ''},
        {colKey: 'width', title: ''}
      ],
      modelShow:false,
      currentIndex:0,
      currentRow:{}
    }
  },
  methods:{
    confirmRow(){
      if (!this.currentRow.colKey || !this.currentRow.title){
        this.$message.error('');
        return;
      }
      const id = new Date().getTime();
      this.attribute.columns.push(Object.assign(this.currentRow,{id: id}));
      this.modelShow = false
    },
    addCol(){
      this.currentRow = {width:0};
      this.modelShow = true
    },
    editRow(row){
      this.currentRow = row;
      this.currentIndex = this.attribute.columns.indexOf(row);
      this.modelShow = true
    },
    delCol(){
      this.attribute.columns.splice(this.currentIndex, 1);
      this.modelShow = false
    }
  }
}
</script>

<style scoped>

</style>
